.root-game {
    text-align: center;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1;
        flex: 1 1;
}

.root-game-logo {
    -webkit-animation: root-game-logo-spin infinite 20s linear;
            animation: root-game-logo-spin infinite 20s linear;
    height: 80px;
}

.root-game-header {
    background-color: #222;
    height: 160px;
    padding: 20px;
    color: white;
}

.root-game-title {
    font-size: 1.5em;
}

.root-game-map-name {
    font-size: 1.3em;
    color: #FFCC00;
}

.game-container {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
}

@-webkit-keyframes root-game-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes root-game-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



